.App {
    text-align: center;
  }
  
  body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  .board-row {
    clear: both;
    content: "";
    display: table;
    height: 33px;
  }
  
  .game-board {
    border-collapse: collapse;
    -ms-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    cursor: default;
  }
  
  .black-square {
    background: black;
    font-size: 0;
  }
  
  .normal-square {
    background: white;
    font-size: 22px;
  }
  
  .highlighted-square {
    background-color: #c9e4ff;
    font-size: 22px;
  }
  
  .normal-square, .black-square, .highlighted-square {
    border: 1px solid #999;
    float: left;
    line-height: 41px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
  }
  
  .normal-square::selection, .black-square::selection, .highlighted-square::selection {
    background-color: transparent;
  }
  
  .black-square:focus {
    outline: none;
    background: #444;
  }
  
  .normal-square:focus {
    background: #dddddd;
    outline: none;
  }
  
  .menu {
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
  }
  
  .menubar {
    display: flex;
    flex-direction: row;
    width: 625px;
    justify-content: space-between;
    margin-top: -5px;
    margin-bottom: -5px;
  }
  
  .menu-button {
    margin-right: 10px;
    border: none;
    border-radius: 4px;
    background-color: white;
    font: 14px "Century Gothic", Futura, sans-serif;
    outline: none;
    padding: 8px;
  }
  
  .menu-button:hover, .menu-button:focus {
    background-color: #c9e4ff;
  }
  
  .square {
    float: left;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    width: 34px;
    position: relative;
  }
  
  .label {
    font-size: 10px;
    font-weight: bold;
    color: black;
    position: absolute;
    height: 0;
    width: 0;
    top: -11px;
    left: 2px;
    pointer-events: none;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .panel-contents {
    text-align: left;
    width: 250px;
  }
  
  .word-length-stats {
    display: grid;
    grid-template-columns: repeat(3, 5em);
  }
  
  .letter-count-stats {
    display: grid;
    grid-template-columns: repeat(4, 3.5em);
  }
  
  dt {
    float: left;
    text-align: right;
    width: 2.5em;
  }
  
  .suggestion-panel {
    margin: 0;
    text-align: left;
  }
  
  .game-info {
    margin-left: 20px;
  }
  
  hr {
    border-top: 1px solid black;
    border-bottom: none;
  }
  
  .suggestion-box {
    margin-bottom: 15px;
    margin-top: 5px;
    font-size: 15px;
    text-align: left;
    list-style-type: none;
    overflow-y: scroll;
    overflow-x: hidden;
    height: 180px;
    width: 18em;
  }
  
  .suggestion-word {
    height: 36px;
  }
  
  h3 {
    margin-bottom: 1px;
    margin-top: 5px;
  }
  
  h4 {
    margin-top: 1px;
  }
  
  .suggestion:hover {
    background: #eee
  }
  